       <template>
            <div id="content">
                <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
                    <div class="item" v-for="(item,index) in list" :key="index">
                        <div class="name">{{item.title}} + {{index}}</div>
                    </div>
                </mt-loadmore>
          </div>
        </template>

<script>
     export default {
        data(){
            return{
                 list:[],
                allLoaded:false,
                page:1
            }
        },
        methods:{
            loadBottom: function(){
                this.$refs.loadmore.onBottomLoaded();
                this.getList();
            },
            getList: function(){
                this.$axios.get(`https://www.apiopen.top/satinGodApi?type=4&page=${this.page}`)
                .then(res=>{
                    this.list = this.list.concat(res.data.data)
                    console.log(this.list)
                    this.page += 1 
                })
                .catch(err=>{
                    console.log(err);
                })
                
                
            }
        },
        mounted: function(){
            this.getList();
        }
    }
</script>
<style scoped>
        #content{
            overflow: scroll;
        }
        .item{

        }
        .item .name{
            height: 50px;
            background-color: green;
            margin-bottom: 10px;
        }
    </style>


